angular.module('Room8.controllers.Scan', [
	'mobile-angular-ui.components.scrollable',
	'Room8.directives.Scan'
    ])

.controller('ScanController', function($scope,$http, $rootScope, $location){

		$scope.myPictures = [];
		$scope.$watch('myPicture', function(value) {
   			if(value) {
     		myPictures.push(value);
   			}
		}, true);


	var pictureSource;   // source de l'image
    var destinationType; // dfinit le format de la valeur retourne

    // Attendre que PhoneGap soit prt
    //
    document.addEventListener("deviceready",onDeviceReady,false);

    // PhoneGap est prt
    //
    function onDeviceReady() {
        pictureSource=navigator.camera.PictureSourceType;
        destinationType=navigator.camera.DestinationType;
    }

    // Appel lorsqu'une photo est bien rcupre
    //
    function onPhotoDataSuccess(imageData) {
      // Dcommentez pour voir le flux image encod en Base64
      // console.log(imageData);

      // Rcuprer l'lment image du DOM
      //
      var smallImage = document.getElementById('smallImage');

      // Rendre visible l'lment image
      //
      smallImage.style.display = 'block';

      // Injecter la photo dans l'lment image
      // Les rgles CSS servent  redimensionner l'image
      //
      smallImage.src = "data:image/jpeg;base64," + imageData;
    }

    // Appel lorsqu'une photo est bien rcupre
    //
    function onPhotoURISuccess(imageURI) {
      // Dcommentez pour voir l'URI du fichier image
      // console.log(imageURI);

      // Rcuprer l'lment image du DOM
      //
      var largeImage = document.getElementById('largeImage');

      // Rendre visible l'lment image
      //
      largeImage.style.display = 'block';

      // Injecter la photo dans l'lment image
      // Les rgles CSS servent  redimensionner l'image
      //
      largeImage.src = imageURI;
    }

    // Un bouton dclenchera l'appel de cette fonction
    //
    function capturePhoto() {
    	console.log("coucou photo")
      // Prendre une photo avec l'appareil photo du mobile et rcuprer l'image sous forme de flux encod en Base64
      navigator.camera.getPicture(onPhotoDataSuccess, onFail, { quality: 50 });
    }

    // Un bouton dclenchera l'appel de cette fonction
    //
    function capturePhotoEdit() {
      // Prendre une photo avec l'appareil photo du mobile, autoriser son dition, et rcuprer l'image sous forme de flux encod en Base64
      navigator.camera.getPicture(onPhotoDataSuccess, onFail, { quality: 20, allowEdit: true }); 
    }

    // Un bouton dclenchera l'appel de cette fonction
    //
    function getPhoto(source) {
      // Rcuprer l'URI d'un fichier image  partir de la source spcifie
      navigator.camera.getPicture(onPhotoURISuccess, onFail, { quality: 50, 
        destinationType: destinationType.FILE_URI,
        sourceType: source });
    }

    // Appel lorsque quelque chose ne tourne pas rond
    // 
    function onFail(message) {
      alert('Echec car : ' + message);
    }

});
